import { mapGetters } from 'vuex';
export default {
  name: 'Logo',
  props: {
    href: {
      type: String,
      default: 'https://github.com/GutsGO'
    },
    target: {
      type: String,
      default: '_blank',
      validator: value => ['_blank', '_self', '_parent', '_top'].includes(value)
    },
    alt: {
      type: String,
      default: 'logo'
    },
    customSrc: {
      type: String,
      default: ''
    }
  },
  computed: {
    ...mapGetters(['userConfig']),
    logoUrl() {
      return this.customSrc || this.userConfig?.logoUrl || require('../assets/logo.webp');
    }
  }
};