import Footer from '@/components/Footer.vue';
import ToggleDark from '@/components/ToggleDark.vue';
import Logo from '@/components/Logo.vue';
import { mapGetters } from 'vuex';
export default {
  name: 'BaseLogin',
  props: {
    title: {
      type: String,
      required: true
    },
    fields: {
      type: Array,
      required: true
    },
    submitText: {
      type: String,
      default: '登录'
    }
  },
  data() {
    return {
      formData: {}
    };
  },
  computed: {
    ...mapGetters(['userConfig'])
  },
  components: {
    Footer,
    ToggleDark,
    Logo
  },
  created() {
    this.initFormData();
  },
  methods: {
    initFormData() {
      const newFormData = {};
      this.fields.forEach(field => {
        newFormData[field.key] = '';
      });
      this.formData = newFormData;
    },
    handleSubmit() {
      this.$emit('submit', {
        ...this.formData
      });
    }
  }
};